import {Container, Nav, Navbar} from "react-bootstrap";
import TreeLogoLight from '../images/tree-logo-light.svg';
import TreeLogoDark from '../images/tree-logo-dark.svg';
import {DarkModeSwitch} from "react-toggle-dark-mode";
import {useState} from "react";


const NavBarApp = (props) => {
    const [bgTheme, setBgTheme] = useState('light');

    const toggleDarkMode = (checked) => {
        props.onToggleDarkMode(checked);

        if (checked) {
            setBgTheme('light');
        } else {
            setBgTheme('dark');
        }
    };

    const goToHomeTabHandler = () => {
        props.onChangeTab('tree');
    }

    const goToGuideTabHandler = () => {
        props.onChangeTab('guide');
    }

    const goToLoginTabHandler = () => {
        props.onChangeTab('login');
    }

    return(
        <Navbar bg={bgTheme} variant={bgTheme} expand="lg">
            <Container>
                <Navbar.Brand href="#home" onClick={goToHomeTabHandler}>
                    <img
                        alt=""
                        src={props.darkMode ? TreeLogoLight : TreeLogoDark}
                        width="30"
                        height="30"
                        className="d-inline-block align-top"
                    />{' '}
                    Arbre genealgic
                </Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="me-auto">
                        <Nav.Link href="#guide" onClick={goToGuideTabHandler}>Guia</Nav.Link>
                        {/*<NavDropdown title="Dropdown" id="basic-nav-dropdown">*/}
                        {/*    <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>*/}
                        {/*    <NavDropdown.Item href="#action/3.2">*/}
                        {/*        Another action*/}
                        {/*    </NavDropdown.Item>*/}
                        {/*    <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>*/}
                        {/*    <NavDropdown.Divider />*/}
                        {/*    <NavDropdown.Item href="#action/3.4">*/}
                        {/*        Separated link*/}
                        {/*    </NavDropdown.Item>*/}
                        {/*</NavDropdown>*/}
                    </Nav>
                    <Nav>
                        <DarkModeSwitch
                            style={{ marginRight: '1rem' }}
                            checked={props.darkMode}
                            onChange={toggleDarkMode}
                            size={30}
                            moonColor={'#4f4f4f'}
                            sunColor={'#f2f2f2'}
                        />
                        <Nav.Link href="#login" onClick={goToLoginTabHandler}>Entrar</Nav.Link>
                    </Nav>
                </Navbar.Collapse>
            </Container>
        </Navbar>
    );
}
export default NavBarApp;

